import React from 'react';
import { EventsListenerFunction } from '../lib/types/events';
import {
  ItemContentData,
  GalleryOptions,
  GallerySelectors,
  ImageMetaDataType,
  VideoMetaDataType,
} from '../lib/types/galleryTypes';

/**
 * React-specific component types
 * These types depend on React and should only be used in React components
 */

/**
 * Function type for providing custom attributes to gallery item elements
 */
export type ItemCustomAttributesFunction = (
  index: number,
) => Record<string, string>;

export interface ItemRendererProps {
  data: ItemContentData;
  options?: GalleryOptions;
  selectors?: GallerySelectors;
  customRenderers?: CustomGalleryRenderers;
  eventsListener?: EventsListenerFunction;
  onItemClick: (event: any, shouldPreventDefault: boolean) => void;
  shouldUseDirectLink: () => boolean;
}

export interface MediaRendererProps {
  data: ItemContentData;
  selectors?: GallerySelectors;
  customRenderers?: CustomGalleryRenderers;
  eventsListener?: EventsListenerFunction;
  options?: GalleryOptions;
}

export interface InfoRendererProps {
  title: string;
  description: string;
  selectors?: GallerySelectors;
  eventsListener?: EventsListenerFunction;
  options?: GalleryOptions;
}

export interface ImageRendererProps {
  data: ItemContentData<ImageMetaDataType>;
  selectors?: GallerySelectors;
  customRenderers?: CustomGalleryRenderers;
  eventsListener?: EventsListenerFunction;
  options?: GalleryOptions;
}

export interface VideoRendererProps {
  data: ItemContentData<VideoMetaDataType>;
  selectors?: GallerySelectors;
  customRenderers?: CustomGalleryRenderers;
  eventsListener?: EventsListenerFunction;
  options?: GalleryOptions;
}

export type CustomRenderersType<T> = React.FC<T>;

export interface CustomGalleryRenderers {
  itemRenderer?: React.FC<ItemRendererProps>;
  mediaRenderer?: React.FC<MediaRendererProps>;
  imageRenderer?: React.FC<ImageRendererProps>;
  videoRenderer?: React.FC<VideoRendererProps>;
  infoRenderer?: React.FC<InfoRendererProps>;
}
